.auth {
    margin: 3rem auto;
    width: 95%;
    max-width: 25rem;
    border-radius: 6px;
    background-color: #4b4d40;
    box-shadow: 0 1px 4px rgba(163, 24, 24, 0.2);
    padding: 1rem;
    text-align: center;
    color: white;
}

/* .form{
    position: relative;
    left: 50px;
} */

.auth h1 {
    text-align: center;
    color: rgb(226, 16, 16);
}

.control {
    margin-bottom: 0.5rem;
}

.control label {
    display: block;
    color: rgb(157, 210, 173);
    font-weight: bold;
    margin-bottom: 0.5rem;
}

.control input {
    font: inherit;
    background-color: #f1e1fc;
    color: #281114;
    border-radius: 4px;
    border: 1px solid white;
    width: 18rem;
    text-align: left;
    padding: 0.25rem;
}

.actions {
    margin-top: 1.rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.actions button {
    cursor: pointer;
    font: inherit;
    color: white;
    background-color: #299e29;
    border: 1px solid #9f5ccc;
    border-radius: 4px;
    padding: 0.5rem 2.5rem;
    margin-top: 20px;
}

.actions button:hover {
    background-color: #873abb;
    border-color: #873abb;
}

.actions .toggle {
    margin: 1rem 0rem;
    background-color: transparent;
    color: #9f5ccc;
    border: none;
    padding: 0.15rem 1.5rem;
}

.actions .toggle:hover {
    background-color: transparent;
    color: #ae82cc;
}